
.phone-app {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: #fff;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  overflow: hidden;
  position: relative;
}

.view-container {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
}

/* ==========  ========== */
.list-view {
  background: #fafafa;
}

.app-header {
  display: flex;
  align-items: center;
  padding: 16px 20px;
  background: linear-gradient(135deg, #66c4d6 0%, #88d4e0 100%);
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  min-height: 60px;
}

.back-btn {
  width: 40px;
  height: 40px;
  border-radius: 8px;
  background: rgba(255, 255, 255, 0.2);
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
  }
}

.header-title {
  flex: 1;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  color: white;
}

.header-spacer {
  width: 40px;
}

.contact-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

.contact-item {
  display: flex;
  align-items: center;
  padding: 12px 20px;
  cursor: pointer;
  transition: background 0.2s;
  position: relative;

  &:hover {
    background: rgba(0, 0, 0, 0.03);
  }

  &:active {
    background: rgba(0, 0, 0, 0.06);
  }
}

.contact-avatar {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #f0f0f0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.contact-name {
  flex: 1;
  margin-left: 16px;
  font-size: 17px;
  font-weight: 500;
  color: #333;
}

.unread-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ff6b8a;
  box-shadow: 0 2px 4px rgba(255, 107, 138, 0.4);
}

/*  */
.bg-watermark {
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
  pointer-events: none;
  opacity: 0.6;
}

.watermark-icon {
  width: 200px;
  height: 200px;
}

/* ==========  ========== */
.call-view {
  background: linear-gradient(180deg, #e8d5e0 0%, #d5c0cc 50%, #c8b0bc 100%);
}

/*  */
.lace-bg {
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(circle at 50% 0%, rgba(255, 255, 255, 0.6) 0%, transparent 30%),
    repeating-linear-gradient(
      0deg,
      transparent 0px,
      transparent 60px,
      rgba(255, 255, 255, 0.15) 60px,
      rgba(255, 255, 255, 0.15) 62px
    ),
    repeating-linear-gradient(
      90deg,
      transparent 0px,
      transparent 60px,
      rgba(255, 255, 255, 0.08) 60px,
      rgba(255, 255, 255, 0.08) 62px
    );
  /*  -  */
  &::before {
    content: '';
    position: absolute;
    top: 15%;
    left: 50%;
    transform: translateX(-50%);
    width: 300px;
    height: 120px;
    background:
      radial-gradient(ellipse 30px 30px at 50% 50%, rgba(255, 255, 255, 0.3), transparent),
      radial-gradient(ellipse 20px 20px at 20% 50%, rgba(255, 255, 255, 0.2), transparent),
      radial-gradient(ellipse 20px 20px at 80% 50%, rgba(255, 255, 255, 0.2), transparent);
  }
}

.call-back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.3);
  border: none;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: all 0.2s;

  &:hover {
    background: rgba(0, 0, 0, 0.4);
    transform: scale(1.05);
  }
}

.call-avatar-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding-top: 60px;
}

.avatar-glow {
  position: absolute;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.5) 0%, transparent 70%);
}

.call-avatar {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid rgba(255, 255, 255, 0.6);
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.15),
    0 0 0 8px rgba(255, 255, 255, 0.2);
  position: relative;
  z-index: 1;
}

.call-name {
  margin-top: 20px;
  font-size: 24px;
  font-weight: 700;
  color: #4a3a42;
  text-shadow:
    1px 1px 0 rgba(255, 255, 255, 0.5),
    -1px -1px 0 rgba(255, 255, 255, 0.5);
  letter-spacing: 2px;
}

.call-content-area {
  background: rgba(255, 255, 255, 0.9);
  border-top-left-radius: 24px;
  border-top-right-radius: 24px;
  padding: 24px 20px;
  min-height: 150px;
  max-height: 40%;
  overflow-y: auto;
}

.call-messages {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.call-message {
  p {
    font-size: 15px;
    line-height: 1.6;
    color: #333;
  }

  &.placeholder p {
    color: #999;
    font-style: italic;
  }
}

/* ==========  ========== */
.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  transition: all 0.3s ease;
}

.slide-left-enter-from {
  transform: translateX(-100%);
  opacity: 0;
}

.slide-left-leave-to {
  transform: translateX(-100%);
  opacity: 0;
}

.slide-right-enter-from {
  transform: translateX(100%);
  opacity: 0;
}

.slide-right-leave-to {
  transform: translateX(100%);
  opacity: 0;
}
