
.chain-app {
  width: 100%;
  height: 100%;
  position: relative;
  background: #fff;
  overflow: hidden;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
}

.view-container {
  position: absolute;
  inset: 0;
  display: flex;
  flex-direction: column;
  background: #fff;
}

/* Header Styles */
.app-header {
  background: #2563eb; /* blue-600 */
  color: white;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  z-index: 20;
}

.header-top {
  padding: 24px 24px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-title {
  font-size: 30px;
  font-weight: 700;
  letter-spacing: 0.025em;
  font-style: italic;
}

.header-actions {
  display: flex;
  gap: 24px;
  color: #bfdbfe; /* blue-100 */
}

.icon-btn {
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    color: white;
    transform: scale(1.1);
  }

  i {
    font-size: 20px;
  }
}

/* Tabs */
.tabs {
  display: flex;
  gap: 32px;
  padding: 0 24px;
  overflow-x: auto;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.tab-btn {
  position: relative;
  padding-bottom: 12px;
  padding-top: 4px;
  background: none;
  border: none;
  color: #bfdbfe;
  font-size: 18px;
  font-weight: 700;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.2s;

  &.active {
    color: white;
  }

  &:hover {
    opacity: 1;
  }
}

.tab-indicator {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 6px;
  background: white;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

/* Chat List */
.chat-list {
  flex: 1;
  overflow-y: auto;
  background: white;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 128px 0;
  color: #9ca3af;

  i {
    font-size: 60px;
    margin-bottom: 16px;
    opacity: 0.2;
  }
  span {
    font-size: 18px;
    font-weight: 500;
  }
}

.chat-item {
  display: flex;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid #f3f4f6;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #f9fafb;
  }
  &:active {
    background-color: #eff6ff;
  }
}

.avatar-container {
  position: relative;
  width: 64px;
  height: 64px;
  margin-right: 16px;
  flex-shrink: 0;
}

.avatar-img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #f3f4f6;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.online-dot {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 16px;
  height: 16px;
  background: #22c55e;
  border: 2px solid white;
  border-radius: 50%;
}

.chat-content {
  flex: 1;
  min-width: 0;
  padding: 4px 0;
}

.chat-info-top {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 6px;
}

.name-row {
  display: flex;
  align-items: center;
  gap: 8px;
}

.chat-name {
  font-size: 18px;
  font-weight: 700;
  color: #1f2937;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.favorite-icon {
  font-size: 14px;
  color: #facc15;
}

.chat-time {
  font-size: 14px;
  color: #9ca3af;
  font-weight: 500;
}

.chat-preview {
  font-size: 16px;
  color: #6b7280;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  line-height: 1.4;

  &.unread {
    color: #1f2937;
    font-weight: 600;
  }
}

.unread-badge {
  margin-left: 12px;
  flex-shrink: 0;
  background: #2563eb;
  color: white;
  font-size: 14px;
  font-weight: 700;
  padding: 4px 12px;
  border-radius: 9999px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  min-width: 28px;
  text-align: center;
}

/* Chat View Styles */
.chat-view {
  background: #f9fafb;
  z-index: 20;
}

.chat-header {
  background: #2563eb;
  color: white;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  z-index: 30;
}

.back-btn,
.menu-btn {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: #1d4ed8;
  }
  i {
    font-size: 20px;
  }
}

.chat-title-container {
  text-align: center;
}

.chat-title {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.025em;
}

.online-status {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
  opacity: 0.9;
  margin-top: 2px;
}

.status-dot {
  width: 8px;
  height: 8px;
  background: #86efac;
  border-radius: 50%;
  box-shadow: 0 0 4px rgba(134, 239, 172, 0.8);
}

.status-text {
  font-size: 12px;
  font-weight: 500;
}

.chat-area {
  flex: 1;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.chat-bg {
  position: absolute;
  inset: 0;
  z-index: 0;
}

.bg-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bg-overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2) 0%, transparent 30%);
}

.messages-container {
  position: relative;
  z-index: 10;
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

.time-divider {
  text-align: center;
  padding: 16px 0;

  span {
    font-size: 12px;
    font-weight: 700;
    color: #6b7280;
    background: rgba(255, 255, 255, 0.8);
    padding: 6px 16px;
    border-radius: 9999px;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  }
}

.message-row {
  display: flex;
  width: 100%;

  &.is-me {
    justify-content: flex-end;
  }
}

/*  */
.sender-name {
  font-size: 12px;
  font-weight: 600;
  color: #6b7280;
  margin-bottom: 4px;
  padding-left: 4px;
}

.message-avatar {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  overflow: hidden;
  border: 2px solid white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  margin-right: 12px;
  flex-shrink: 0;
  align-self: flex-end;
  margin-bottom: 4px;
  background: #e5e7eb;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.message-bubble-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 4px;

  &:hover .message-actions {
    opacity: 1;
    pointer-events: auto;
  }
}

/*  */
.is-me .message-bubble-wrapper {
  align-items: flex-end;
}

/*  */
.sender-name {
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  margin-bottom: 2px;
  padding-left: 12px;
}

/*  */
.sticker-image {
  max-width: 100px;
  max-height: 100px;
  object-fit: contain;
  display: block;
  margin-bottom: 4px;
}

/*  -  */
.favorite-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
  font-size: 18px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  margin-right: 12px; /*  */

  &:hover {
    background: rgba(255, 255, 255, 0.3);
    transform: scale(1.1);
  }

  &.active {
    color: #ff4d6d;
    background: rgba(255, 77, 109, 0.2);
  }
}

/*  */
.bubble-me {
  min-width: 60px;
}

/* */
.sticker-bubble {
  padding: 8px;
  border-radius: 16px;
  background: rgba(255, 255, 255, 0.9);
  margin-bottom: 8px;

  &.bubble-me {
    background: rgba(232, 245, 253, 0.95);
  }
}

/*  */
.sticker-image {
  display: block;
  max-width: 120px;
  max-height: 120px;
  object-fit: contain;
}

/*  */
.sticker-time {
  font-size: 11px;
  color: rgba(0, 0, 0, 0.4);
  text-align: right;
  margin-top: -4px;
}

/*  */
.sticker-image {
  display: block;
  max-width: 120px;
  max-height: 120px;
  object-fit: contain;
  margin-bottom: 8px;
}

.message-actions {
  display: flex;
  gap: 4px;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.2s;
}

.action-btn {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  border: none;
  background: rgba(0, 0, 0, 0.1);
  color: #6b7280;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;

  &:hover {
    background: rgba(0, 0, 0, 0.2);
    color: #374151;
  }

  &.delete:hover {
    background: #fee2e2;
    color: #dc2626;
  }
}

.edit-textarea {
  width: 100%;
  min-width: 200px;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 14px;
  resize: vertical;
  font-family: inherit;
}

.edit-actions {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

.edit-btn {
  padding: 6px 12px;
  border-radius: 6px;
  border: none;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;

  &.save {
    background: #3b82f6;
    color: white;

    &:hover {
      background: #2563eb;
    }
  }

  &.cancel {
    background: #e5e7eb;
    color: #374151;

    &:hover {
      background: #d1d5db;
    }
  }
}

.message-bubble {
  position: relative;
  max-width: 75%;
  padding: 12px 20px;
  border-radius: 16px;
  font-size: 16px;
  line-height: 1.6;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  word-break: break-word;
}

.bubble-me {
  background: #dbeafe; /* blue-100 */
  color: #111827;
  border-bottom-right-radius: 2px;
}

.bubble-other {
  background: white;
  color: #111827;
  border-bottom-left-radius: 2px;
}

.message-time {
  text-align: right;
  margin-top: 6px;
  opacity: 0.5;
  font-size: 12px;
  font-weight: 500;
}

/*  */
.bilingual-message {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.text-jp {
  font-size: 15px;
  color: #374151;
  line-height: 1.5;
}

.text-cn {
  font-size: 14px;
  color: #6b7280;
  line-height: 1.4;
  padding-top: 4px;
  border-top: 1px dashed #e5e7eb;
}

.message-image {
  max-width: 180px;
  max-height: 180px;
  border-radius: 8px;
  object-fit: contain;
  display: block;
  margin-bottom: 4px;
}

.input-area {
  background: white;
  padding: 8px 12px;
  border-top: 1px solid #f3f4f6;
  z-index: 30;
  box-shadow: 0 -4px 6px -1px rgba(0, 0, 0, 0.05);
}

.input-wrapper {
  display: flex;
  align-items: center;
  gap: 8px;
  background: #f3f4f6;
  border-radius: 24px;
  padding: 6px 10px;
}

.plus-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: none;
  color: #9ca3af;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: color 0.2s;

  &:hover {
    color: #2563eb;
  }
  i {
    font-size: 20px;
  }
}

.message-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  font-size: 15px;
  color: #1f2937;
  resize: none;
  padding: 4px 0;
  min-height: 20px;
  max-height: 80px;
  font-family: inherit;
  line-height: 1.4;
}

.send-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: #d1d5db;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;

  &.active {
    background: #2563eb;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);

    &:hover {
      transform: scale(1.05);
    }
  }

  i {
    font-size: 18px;
    margin-left: 2px;
  }
}

/*  */
.sticker-panel {
  background: white;
  border-radius: 16px 16px 0 0;
  box-shadow: 0 -4px 20px rgba(0, 0, 0, 0.1);
  padding: 12px;
  max-height: 250px;
  overflow-y: auto;
}

.sticker-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;

  span {
    font-size: 14px;
    font-weight: 600;
    color: #374151;
  }
}

.upload-local-btn {
  display: inline-flex;
  align-items: center;
  gap: 4px;
  padding: 6px 12px;
  background: #f3f4f6;
  border-radius: 8px;
  font-size: 12px;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: #e5e7eb;
    color: #374151;
  }

  i {
    font-size: 12px;
  }
}

.sticker-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 8px;
}

.sticker-item {
  width: 100%;
  aspect-ratio: 1;
  object-fit: contain;
  border-radius: 8px;
  background: #f9fafb;
  cursor: pointer;
  transition: all 0.2s;
  padding: 4px;

  &:hover {
    background: #e5e7eb;
    transform: scale(1.1);
  }
}

/*  */
.pending-image-area {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: #f9fafb;
  border-radius: 12px;
  margin-bottom: 8px;
}

.pending-image {
  height: 60px;
  max-width: 120px;
  object-fit: contain;
  border-radius: 8px;
}

.remove-pending {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: none;
  background: #ef4444;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s;

  &:hover {
    background: #dc2626;
  }
}

/* Slide up transition */
.slide-up-enter-active,
.slide-up-leave-active {
  transition: all 0.25s ease;
}

.slide-up-enter-from,
.slide-up-leave-to {
  opacity: 0;
  transform: translateY(20px);
}

/*  */
.create-group-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 16px;
  margin: 12px 16px;
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.3);

  &:hover {
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
  }
}

/*  */
.group-avatar-container {
  width: 56px;
  height: 56px;
  margin-right: 16px;
  flex-shrink: 0;
}

.group-avatars {
  width: 100%;
  height: 100%;
  display: grid;
  gap: 2px;
  border-radius: 12px;
  overflow: hidden;
  background: #e5e7eb;

  &.avatars-1 {
    grid-template: 1fr / 1fr;
  }
  &.avatars-2 {
    grid-template: 1fr / 1fr 1fr;
  }
  &.avatars-3 {
    grid-template: 1fr 1fr / 1fr 1fr;
    .group-avatar-img:first-child {
      grid-column: span 2;
    }
  }
  &.avatars-4 {
    grid-template: 1fr 1fr / 1fr 1fr;
  }
}

.group-avatar-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  */
.group-item {
  position: relative;
}

.member-count {
  font-size: 12px;
  color: #6b7280;
  background: #f3f4f6;
  padding: 2px 8px;
  border-radius: 10px;
}

.last-sender {
  color: #3b82f6;
  font-weight: 500;
}

.delete-group-btn {
  position: absolute;
  right: 12px;
  top: 50%;
  transform: translateY(-50%);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  border: none;
  background: transparent;
  color: #9ca3af;
  font-size: 14px;
  cursor: pointer;
  opacity: 0;
  transition: all 0.2s;

  .chat-item:hover & {
    opacity: 1;
  }

  &:hover {
    background: #fee2e2;
    color: #dc2626;
  }
}

/*  */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100;
  padding: 20px;
}

.modal-content {
  background: white;
  border-radius: 16px;
  padding: 24px;
  max-width: 400px;
  width: 100%;
  max-height: 80vh;
  overflow-y: auto;
}

.modal-title {
  font-size: 20px;
  font-weight: 700;
  color: #1f2937;
  margin-bottom: 20px;
  text-align: center;
}

.form-group {
  margin-bottom: 20px;

  label {
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: #374151;
    margin-bottom: 8px;
  }
}

.form-input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #d1d5db;
  border-radius: 10px;
  font-size: 15px;
  transition: all 0.2s;

  &:focus {
    outline: none;
    border-color: #3b82f6;
    box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
  }
}

.member-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
  max-height: 300px;
  overflow-y: auto;
}

.member-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
  padding: 12px 8px;
  border-radius: 12px;
  background: #f9fafb;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;

  &:hover {
    background: #f3f4f6;
  }

  &.selected {
    background: #dbeafe;
    border: 2px solid #3b82f6;
  }
}

.member-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}

.member-name {
  font-size: 12px;
  color: #374151;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.check-icon {
  position: absolute;
  top: 8px;
  right: 8px;
  color: #3b82f6;
  font-size: 12px;
}

.modal-actions {
  display: flex;
  gap: 12px;
  margin-top: 24px;
}

.btn-cancel,
.btn-confirm {
  flex: 1;
  padding: 12px;
  border-radius: 10px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-cancel {
  background: #f3f4f6;
  border: none;
  color: #374151;

  &:hover {
    background: #e5e7eb;
  }
}

.btn-confirm {
  background: #3b82f6;
  border: none;
  color: white;

  &:hover:not(:disabled) {
    background: #2563eb;
  }

  &:disabled {
    background: #9ca3af;
    cursor: not-allowed;
  }
}

/*  */
.settings-btn {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  border: none;
  background: rgba(255, 255, 255, 0.2);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s;
  margin-right: 8px;

  &:hover {
    background: rgba(255, 255, 255, 0.3);
  }

  i {
    font-size: 16px;
  }
}

/*  */
.group-settings-modal {
  max-width: 450px;
}

.avatar-upload-row,
.bg-upload-row {
  display: flex;
  align-items: center;
  gap: 16px;
}

.current-avatar {
  width: 64px;
  height: 64px;
  border-radius: 12px;
  overflow: hidden;
  background: #f3f4f6;
  flex-shrink: 0;
}

.preview-avatar {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.bg-preview {
  width: 120px;
  height: 68px;
  border-radius: 8px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  flex-shrink: 0;

  span {
    font-size: 12px;
    color: white;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  }
}

.upload-btns {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.upload-btn {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  padding: 8px 14px;
  border-radius: 8px;
  background: #f3f4f6;
  border: none;
  font-size: 13px;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s;

  &:hover {
    background: #e5e7eb;
  }

  &.reset {
    color: #dc2626;
  }

  i {
    font-size: 12px;
  }
}

.member-grid.compact {
  grid-template-columns: repeat(4, 1fr);
  max-height: 200px;

  .member-option {
    padding: 8px 4px;
  }

  .member-avatar {
    width: 36px;
    height: 36px;
  }

  .member-name {
    font-size: 10px;
  }
}

.delete-group-text-btn {
  margin-top: 16px;
  width: 100%;
  padding: 12px;
  border: none;
  background: transparent;
  color: #dc2626;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
  border-radius: 8px;

  &:hover {
    background: #fee2e2;
  }

  i {
    margin-right: 6px;
  }
}

/* Fade transition */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.list-padding {
  height: 80px;
}

/* Transitions */
.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  transition: all 0.3s ease-out;
}

.slide-left-enter-from {
  transform: translateX(100%);
}
.slide-left-leave-to {
  transform: translateX(-20%);
  opacity: 0.8;
}

.slide-right-enter-from {
  transform: translateX(-20%);
  opacity: 0.8;
}
.slide-right-leave-to {
  transform: translateX(100%);
  z-index: 10;
}
