  border-radius: 15px;
  overflow-x: auto;
}

.card-thumb {
  width: 100px;
  aspect-ratio: 3 / 4;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
  border: 3px solid rgba(255, 255, 255, 0.2);
