  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 350px;
  max-width: 600px;
}

.skills-title {
  color: #fff;
  font-size: clamp(20px, 2.5vw, 28px);
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 12px;
  padding-bottom: 15px;
  border-bottom: 3px solid rgba(255, 105, 180, 0.5);

  i {
    color: #ffd700;
  }
}

.skills-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  overflow-y: auto;
  padding-right: 10px;

  /*  */
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background: rgba(255, 105, 180, 0.5);
    border-radius: 10px;

    &:hover {
      background: rgba(255, 105, 180, 0.7);
    }
  }
}

.skill-item {
  display: flex;
  gap: 15px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s;

  &:hover {
    background: rgba(255, 105, 180, 0.1);
    border-color: rgba(255, 105, 180, 0.5);
    transform: translateX(5px);
  }
}

.skill-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: linear-gradient(135deg, rgba(255, 215, 0, 0.7), rgba(255, 165, 0, 0.7));
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  border: 2px solid rgba(255, 255, 255, 0.3);

  i {
    color: #fff;
    font-size: 24px;
  }
}

.skill-content {
  flex: 1;
}

.skill-name {
  color: #fff;
  font-size: clamp(14px, 1.8vw, 18px);
  font-weight: bold;
  margin-bottom: 8px;
}

.skill-description {
  color: rgba(255, 255, 255, 0.8);
  font-size: clamp(12px, 1.5vw, 15px);
  line-height: 1.6;
}

.no-skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: 60px 20px;
  color: rgba(255, 255, 255, 0.4);

  i {
    font-size: 60px;
  }

  p {
    font-size: 18px;
  }
}

/* =====  ===== */
.music-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(20, 30, 48, 0.98), rgba(36, 59, 85, 0.98));
  backdrop-filter: blur(20px);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: fadeIn 0.4s ease-out;
}

.music-back-btn {
  position: absolute;
  top: clamp(20px, 3vw, 40px);
  left: clamp(20px, 3vw, 40px);
  padding: clamp(10px, 1.5vw, 15px) clamp(20px, 2.5vw, 30px);
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 30px;
  color: #fff;
  font-size: clamp(14px, 1.8vw, 18px);
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s;
  z-index: 10;

  &:hover {
    background: rgba(255, 105, 180, 0.6);
    border-color: rgba(255, 105, 180, 0.8);
    transform: translateX(-5px);
  }

  i {
    font-size: clamp(16px, 2vw, 20px);
  }
}

.music-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  padding: clamp(30px, 4vw, 50px) 20px clamp(20px, 3vw, 30px);
  border-bottom: 2px solid rgba(255, 255, 255, 0.1);

  .music-icon {
    width: clamp(50px, 6vw, 70px);
    height: clamp(50px, 6vw, 70px);
    border-radius: 50%;
    background: linear-gradient(135deg, rgba(255, 105, 180, 0.8), rgba(255, 20, 147, 0.8));
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 5px 20px rgba(255, 105, 180, 0.5);

    i {
      color: #fff;
      font-size: clamp(24px, 3vw, 32px);
    }
  }

  h2 {
    color: #fff;
    font-size: clamp(28px, 4vw, 42px);
    font-weight: bold;
    text-shadow: 0 2px 10px rgba(255, 105, 180, 0.5);
    margin: 0;
  }
}

/*  */
.music-content {
  flex: 1;
  display: flex;
  gap: clamp(30px, 4vw, 50px);
  padding: clamp(30px, 4vw, 50px);
  overflow: hidden;
}

/*  */
.album-cover-section {
  flex: 0 0 clamp(300px, 35vw, 500px);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: clamp(20px, 3vw, 30px);
}

.album-cover-frame {
  width: 100%;
  aspect-ratio: 1 / 1;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.05));
  border-radius: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  overflow: hidden;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
    transform: rotate(45deg);
    animation: shine 3s infinite;
  }
}

@keyframes shine {
  0%,
  100% {
    transform: rotate(45deg) translateY(-100%);
  }
  50% {
    transform: rotate(45deg) translateY(100%);
  }
}

.album-cover-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.album-cover-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.3);

  i {
    font-size: clamp(80px, 10vw, 120px);
  }
}

/* =====  ===== */
.gacha-page {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(20, 30, 48, 0.98), rgba(36, 59, 85, 0.98));
