
.phone-peek {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

/*  */
.peek-viewport {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: transparent;
}

/*  */
.device {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(var(--peek-x), var(--peek-y)) scale(var(--peek-scale));
  transform-origin: top left;
}

/*  */
.device-frame {
  width: 100%;
  height: 100%;
  border-top-left-radius: 32px;
  border-top-right-radius: 32px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  padding: 6px 6px 0 6px;
  background: linear-gradient(135deg, #f2f2f2 0%, #9b9b9b 18%, #3a3a3a 48%, #bdbdbd 78%, #f7f7f7 100%);
  box-shadow:
    0 -8px 32px rgba(0, 0, 0, 0.3),
    inset 0 0 0 2px rgba(255, 255, 255, 0.4);
}

/*  */
.device-bezel {
  width: 100%;
  height: 100%;
  border-top-left-radius: 28px;
  border-top-right-radius: 28px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  background: rgba(0, 0, 0, 0.92);
  padding: 8px 8px 0 8px;
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.08);
}

/*  */
.device-screen {
  width: 100%;
  height: 100%;
  border-top-left-radius: 22px;
  border-top-right-radius: 22px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  overflow: hidden;
  position: relative;
}

/*  */
.wallpaper {
  position: absolute;
  inset: 0;
  background:
    radial-gradient(900px 700px at 30% 20%, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0) 55%),
    linear-gradient(135deg, #070822 0%, #071a57 42%, #056b80 100%);
}

.wallpaper::before,
.wallpaper::after {
  content: '';
  position: absolute;
  inset: -20%;
  transform: rotate(-14deg);
  background: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.08), rgba(255, 255, 255, 0));
  opacity: 0.4;
}

.wallpaper::after {
  transform: rotate(-14deg) translateY(200px);
  opacity: 0.25;
}

/*  */
.status-bar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 44px;
  padding: 12px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  font-size: 14px;
  font-weight: 600;
  z-index: 10;
}

.status-icons {
  display: flex;
  gap: 8px;
  font-size: 12px;
}

/* App  */
.apps-grid {
  position: absolute;
  inset: 80px 32px auto 32px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas:
    'twesta chain'
    'phone ourstream';
  gap: 24px;
  align-items: start;
  justify-items: center;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  transition: transform 0.15s ease;
}

.app:hover {
  transform: scale(1.05);
}

.app:active {
  transform: scale(0.95);
}

.icon {
  --icon-size: clamp(64px, 18vw, 80px);
  width: var(--icon-size);
  height: var(--icon-size);
  border-radius: 20px;
  position: relative;
  display: grid;
  place-items: center;
  overflow: hidden;
  box-shadow:
    0 12px 24px rgba(0, 0, 0, 0.3),
    inset 0 0 0 1px rgba(255, 255, 255, 0.2);
}

.label {
  font-size: clamp(12px, 3vw, 14px);
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 0.5px;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.4);
  font-weight: 500;
}

/*  */
.badge {
  position: absolute;
  top: 4px;
  right: 4px;
  width: 18px;
  height: 18px;
  border-radius: 999px;
  background: #ff3b30;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  z-index: 5;
}

.badge::after {
  content: '';
  position: absolute;
  inset: -3px;
  border-radius: 999px;
  border: 3px solid rgba(255, 255, 255, 0.9);
}

/* App  */
.icon--twesta {
  background: linear-gradient(135deg, #79c8ff 0%, #b48cff 52%, #ff6aa7 100%);
}

.icon--chain {
  background: linear-gradient(135deg, #6ee9ff 0%, #74c6ff 100%);
}

.icon--phone {
  background: linear-gradient(135deg, #63ff6a 0%, #1fe05a 100%);
  color: #2bbf56;
}

.icon--ourstream {
  background: linear-gradient(135deg, #ff004a 0%, #ff3f79 100%);
}

.icon-svg {
  width: 70%;
  height: 70%;
}

/* CHAIN  */
.chain-bubble {
  width: 75%;
  height: 55%;
  background: rgba(255, 255, 255, 0.98);
  border-radius: 14px;
  position: relative;
  display: grid;
  place-items: center;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
}

.chain-text {
  font-weight: 800;
  letter-spacing: 1.5px;
  font-size: clamp(12px, 3.5vw, 16px);
  color: rgba(85, 210, 236, 0.95);
}

.chain-tail {
  position: absolute;
  left: 16px;
  bottom: -8px;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 12px solid rgba(255, 255, 255, 0.98);
}

/* OurSTREAM  */
.our-mark {
  width: 70%;
  height: 60%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  color: rgba(255, 255, 255, 0.98);
}

.our-play {
  width: 28px;
  height: 28px;
  border-radius: 999px;
  border: 4px solid rgba(255, 255, 255, 0.98);
  display: grid;
  place-items: center;
}

.our-tri {
  width: 0;
  height: 0;
  border-left: 10px solid rgba(255, 255, 255, 0.98);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  margin-left: 3px;
}

.our-ur {
  font-weight: 900;
  font-size: 28px;
  letter-spacing: -1px;
}

/*  */
.settings-btn {
  position: absolute;
  bottom: 24px;
  right: 24px;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(8px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 10;

  &:hover {
    background: rgba(255, 255, 255, 0.25);
    color: white;
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }
}

/* Home  */
.home-indicator {
  position: absolute;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 4px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 2px;
}

/*  */
@media (max-width: 380px) {
  .apps-grid {
    inset: 60px 20px auto 20px;
    gap: 16px;
  }
  .icon {
    border-radius: 16px;
  }
}
