
.gacha-selection-modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 100000;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.modal-content {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  border-radius: 20px;
  padding: 30px;
  min-width: 500px;
  max-width: 90vw;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.modal-header {
  text-align: center;
  margin-bottom: 25px;

  h2 {
    font-size: 24px;
    color: #ffd700;
    margin: 0 0 8px;
  }

  .subtitle {
    color: #aaa;
    font-size: 14px;
    margin: 0;
  }
}

.selection-options {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 25px;
}

.option-btn {
  flex: 1;
  max-width: 180px;
  padding: 25px 20px;
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.05);
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;

  &:hover {
    border-color: rgba(255, 255, 255, 0.4);
    background: rgba(255, 255, 255, 0.1);
    transform: translateY(-3px);
  }

  &.selected {
    border-color: #ffd700;
    background: rgba(255, 215, 0, 0.1);
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
  }
}

.option-icon {
  font-size: 48px;
  margin-bottom: 12px;
}

.option-label {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 6px;
}

.option-desc {
  font-size: 12px;
  color: #888;
}

// 31
.pick-options {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 25px;
}

.pick-card {
  width: 140px;
  border-radius: 12px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 3px solid rgba(255, 255, 255, 0.2);
  background: rgba(0, 0, 0, 0.3);

  &:hover {
    transform: translateY(-5px) scale(1.05);
    box-shadow: 0 15px 40px rgba(0, 0, 0, 0.4);
  }

  &.selected {
    border-color: #ffd700;
    box-shadow: 0 0 25px rgba(255, 215, 0, 0.5);
    transform: scale(1.1);
  }

  &.ssr {
    border-color: #ffd700;
  }
  &.sr {
    border-color: #c0c0c0;
  }
  &.r {
    border-color: #cd7f32;
  }
  &. {
    border-color: #ffd700;
  }
  &. {
    border-color: #c0c0c0;
  }
  &. {
    border-color: #666;
  }
}

.pick-image {
  width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  display: block;
}

.pick-info {
  padding: 10px;
  text-align: center;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.5));
}

.pick-rarity {
  display: block;
  font-size: 11px;
  color: #ffd700;
  font-weight: bold;
  margin-bottom: 2px;
}

.pick-name {
  display: block;
  font-size: 12px;
  color: #fff;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

// 
.result-display {
  margin-bottom: 25px;
  animation: slideUp 0.5s ease;
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.result-card {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 20px;
  border-radius: 15px;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);

  &.ssr,
  &. {
    border-color: #ffd700;
    background: rgba(255, 215, 0, 0.1);
  }
  &.sr,
  &. {
    border-color: #c0c0c0;
    background: rgba(192, 192, 192, 0.1);
  }
}

.result-image {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
}

.result-info {
  flex: 1;
}

.result-rarity {
  font-size: 14px;
  color: #ffd700;
  font-weight: bold;
  margin-bottom: 6px;
}

.result-name {
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  margin-bottom: 6px;
}

.result-effect {
  font-size: 13px;
  color: #aaa;
  line-height: 1.4;
}

.modal-actions {
  display: flex;
  justify-content: center;
}

.confirm-btn {
  padding: 14px 50px;
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 25px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 10px;
  transition: all 0.3s ease;

  &:hover:not(:disabled) {
    transform: scale(1.05);
    box-shadow: 0 5px 25px rgba(102, 126, 234, 0.5);
  }

  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }

  i {
    font-size: 18px;
  }
}

// 
.effect-detail-panel {
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(255, 255, 255, 0.15);
  border-radius: 12px;
  padding: 15px;
  margin-bottom: 20px;
  animation: slideDown 0.3s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.effect-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.effect-card-name {
  font-size: 16px;
  font-weight: bold;
  color: #ffd700;
}

.effect-cost {
  padding: 3px 10px;
  background: rgba(102, 126, 234, 0.3);
  border-radius: 10px;
  font-size: 12px;
  color: #a8c0ff;

  i {
    font-size: 11px;
  }
}

.effect-type {
  padding: 3px 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  font-size: 12px;
  color: #aaa;
}

.effect-entries {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.effect-entry {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 8px 10px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;

  &.consumption {
    background: rgba(255, 100, 100, 0.1);
    border-left: 3px solid #ff6464;
  }

  &.drink-effect {
    background: rgba(100, 200, 255, 0.1);
  }
}

.effect-icon {
  width: 22px;
  height: 22px;
  flex-shrink: 0;
}

.effect-bullet {
  color: #ffd700;
  font-size: 14px;
  flex-shrink: 0;
}

.effect-text {
  font-size: 13px;
  color: #ddd;
  line-height: 1.5;
}

// script
