
.produce-host {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
}

// 
.battle-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  display: flex;
  flex-direction: column;
  z-index: 10001;

  .battle-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background: rgba(0, 0, 0, 0.3);

    span {
      color: white;
      font-size: 24px;
      font-weight: bold;
      text-transform: uppercase;
    }

    .battle-close {
      background: rgba(255, 255, 255, 0.2);
      border: none;
      color: white;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      cursor: pointer;
      font-size: 18px;

      &:hover {
        background: rgba(255, 255, 255, 0.3);
      }
    }
  }

  .battle-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;

    p {
      color: white;
      font-size: 20px;
      margin: 0;
    }

    .battle-complete-btn {
      padding: 15px 40px;
      font-size: 18px;
      background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
      border: none;
      border-radius: 10px;
      color: white;
      cursor: pointer;
      margin-top: 30px;

      &:hover {
        transform: scale(1.05);
      }
    }
  }
}
